
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import {message, messageStatus} from '../config' // messageStatus
export default {
  name: 'message',
  components: {
    Group,
    Cell,
    Badge
  },
  data () {
    return {
      list: []
    }
  },
  created () {
    if (this.$localStorage.get('logined') !== 'false') {
      this.getList()
    }
  },
  methods: {
    // handleChange () {
    //   console.log(messageStatus)
    // },
    handleRead (id) {
      this.$http({
        method: 'jsonp',
        url: messageStatus,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: {
          userId: JSON.parse(this.$localStorage.get('userInfo')).userId,
          messageId: id
        }
      })
      .then(res => {
        console.log(res)
        if (res.body.status) {
          this.$vux.toast.show({
            type: 'text',
            width: '10em',
            position: 'bottom',
            text: '消息已读',
            time: '1000'
          })
          this.getList()
        } else {
          this.$vux.toast.show({
            type: 'text',
            width: '10em',
            position: 'bottom',
            text: res.body.msg,
            time: '1000'
          })
        }
      })
    },
    getList () {
      this.$http({
        method: 'jsonp',
        url: message,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: {
          userId: JSON.parse(this.$localStorage.get('userInfo')).userId,
          // userId: '0e9236d6b4474f259cfd1d2f9bf8f3b0',
          limit: 10,
          pageIndex: 0
        }
      })
      .then(res => {
        res.body.data.messageList.forEach(el => {
          el.createTime = dateFormat(el.createTime)
        })
        if (res.body.status) {
          this.list = res.body.data.messageList
        } else {
          this.$vux.toast.show({
            type: 'text',
            width: '10em',
            position: 'bottom',
            text: res.body.msg,
            time: '1000'
          })
        }
      })
    }
  }
}



